/**
 * Global html and document element utilities
 */
export class DocumentUtils {
  /**
   * Gets browser's default font size \
   * Can be used for correct rem conversion to pixels
   */
  static getDefaultFontSize(): number {
    const element = document.createElement('div');
    element.style.width = '1rem';
    element.style.display = 'none';
    document.body.append(element);
    
    const widthMatch = getComputedStyle(element)?.getPropertyValue('width')?.match(/\d+/);

    element.remove();

    return Number(widthMatch && widthMatch.length > 0 ? widthMatch[0] : 16);
  }

  /**
   * Converts the string size value to pixels.
   */
  static convertToPixels(value: string): number {
    // Gets the first sequence of digits as numeric value. (32px => 32, a3rem48px => 3).
    let numericValue = Number(value.match(/\d*\.?\d*/)[0]);
    if (value.includes('rem')) {
      numericValue *= this.getDefaultFontSize();
    }
    
    return numericValue;
  }
}
